import React from 'react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import Collapsible from 'react-collapsible';
import SectionTitles from '../components/shared/section-titles';
import constants from '../constants/coverage';
import GoogleMapComponent from '../components/shared/google-map';
import BottomCta from "../components/shared/bottom-cta"
import { Link } from 'gatsby';

const CoveragePage = () => {

  return (
    <>
      <Layout>
        <SEO title='Покритие' />

        <div style={{ paddingTop: '12rem' }}>
          <SectionTitles
            undertext={'Пълен достъп и контрол до използваните от теб телевизия и интернет услуги – честно, не ти трябва друго мобилно приложение за стрийминг, освен NetSurf Play.'}>
            <h1>Покриваме те в над 30<br />населени места</h1>
            <hr className='center' />
          </SectionTitles>

          <div className='container'>
            <div className='my-5'>
              <GoogleMapComponent height={600} />
            </div>
            <div className='d-flex justify-content-center text-center mb-5'><h2>Всички областни<br />градове</h2></div>
            <div className='d-flex justify-content-center'>
              <div className='col-12 col-lg-9 col-xl-7'>
                {constants.places.map((place, index) => (
                  <CElement
                    {...place}
                    isInitiallyOpen={index === 0}
                  />
                ))}
              </div>
            </div>

            <BottomCta
              bgColor={'transparent'}>
              <h2 className='mb-md-5 mb-2 d-none d-md-block'>Нямаме търпение да ти покажем<br /><span className='text-purple'> колко сме добри!</span></h2>
              <h2 className='mb-md-5 mb-2 d-block d-md-none'>Нямаме търпение да ти покажем<span className='text-purple'> колко сме добри!</span></h2>
              <p className='mb-md-5 mb-3'>Нашите пакети – по-благи от тези под коледната елха</p>
              <Link to={'/za-doma'} className='btn btn-primary green'>Виж пакетите</Link>
            </BottomCta>
          </div>
        </div>
      </Layout>
    </>
  )
}

const CElement = ({ mainPlace, subPlaces, isInitiallyOpen }) => {
  return (
    <>
      <Collapsible
        open={isInitiallyOpen}
        trigger={
          <CHeader isOpen={false} places={subPlaces} title={mainPlace} />
        }
        triggerWhenOpen={
          <CHeader isOpen={true} places={subPlaces} title={mainPlace} />
        }
      >
        <CContent
          places={subPlaces}
        />
      </Collapsible>
    </>
  )
}

const CContent = ({ places }) => {
  return (
    <>
      <div className='row'>
        {places.map((place, index) => (
          <div className='col-6 col-md-3' key={place + index}>
            <p><span className='icon map-pin'></span>{place}</p>
          </div>
        ))}
      </div>
    </>
  );
}

const CHeader = ({ isOpen, title, places }) => {
  return (
    <>
      <div className='row w-100'>
        <button className='clear w-100'>
          <div className='d-flex justify-content-between mb-0'>
            <h3 className={'text-left'}><span className='icon green-tick mr-2'></span>{title}</h3>
            <p className={'text-left'}>+{places.length} области<span className={'icon ml-2 ' + (!isOpen ? 'arrow-close' : 'arrow-open')}></span></p>
          </div>
          <hr className='normal-bottom m-0 p-0' />
        </button>
      </div>
    </>
  )
}


export default CoveragePage;